import {Paper, Grid, Button, AppBar, TextField, Typography, Toolbar} from '@material-ui/core'
import {withStyles} from '@material-ui/core/styles'
import React, { Component } from 'react';

//Whatever is passed in from the header check and provide links as followed
const styles = {
    header:{
        padding: 0,
        margin: 0,
        color: 'white',
    },
    menubutton:{
        padding: 0,
        margin: 0,
        color: 'blue',
        fontSize: 'x-small',
    },
};

class dailyAppBar extends Component{
    render(){
        return(
            <AppBar position="static">
            <Typography className={this.props.classes.header}>Login</Typography>
                <Toolbar>
                    <Button className={this.props.classes.menubutton}>Home</Button>
                    <Button className={this.props.classes.menubutton}>About</Button>
                </Toolbar>
            </AppBar>
        );
    }
}

export default withStyles(styles)(dailyAppBar);



